
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}

body {
    background: black
}

h1, h2, h3, p, a {
    color: wheat;
    text-decoration: none;
    padding-bottom: 1.5rem;
}

ul {
    list-style-type: none;
}

.btn {
    padding: 10px 24px;
    font-size: 1rem;
    background: #335af9;
    color: white;
    border: 1px solid gray;
    border-radius: 7%;
    font-weight: 500;
    cursor: pointer;
    box-shadow: 1px 1px 1px rgb(134, 131, 131);
}

.btn:hover {
    background: #74c0fc;
    color: white;
    transition: 0.9s;
}



.nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 1rem 0;
    width: 100%;
    /* z-index: 1; */
}

.nav-bg {
    background-color: rbga(0, 0, 0, 0.9);
    transition: 0.5s;
}

.nav-menu {
    display: flex;
    color: wheat;
}

.nav-menu li {
    padding: 0, 1rem;
}

.nav-menu li a {
    font-size: 1rem;
    font-weight: 500;
    padding: 0 1rem;
}

.nav-menu li a:hover {
    color: #74c0fc;
    transition: 0.5s;
}

.burgers {
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.9);
        color: wheat;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 10000;
        transition: 0.5s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .burgers {
        display: initial;
    }
}
.homebody {
  height: 80vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  }

  
  .homebody .content h1 {
    font-size: 3rem;
    padding: 0.5rem 0 1.4rem;
    text-shadow: 1.5px 1.5px 1.5px rgb(41, 40, 40);
    -webkit-animation-name: appear;
            animation-name: appear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }


  .content h1 {
    font-size: 2.5rem;
    text-shadow: 2.5px 2.5px 2.5px rgb(41, 40, 40);
    font-weight: 600;
    -webkit-animation-name: appear;
            animation-name: appear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }


  .content .btn {
    margin-right: 2rem;
  }

@-webkit-keyframes appear {
  from { -webkit-transform: translateX(-40rem); transform: translateX(-40rem) }
  to { -webkit-transform: translateY(0); transform: translateY(0)}
}

@keyframes appear {
  from { -webkit-transform: translateX(-40rem); transform: translateX(-40rem) }
  to { -webkit-transform: translateY(0); transform: translateY(0)}
}

@media screen and (max-width: 390px) {
  .homebody .content h1 {
    font-size: 3rem;
  }
  .homebody .content p {
    font-size: 1.2rem;
  }
}

@media screen and (width: 575px) {
  .homebody .content h1 {
    font-size: 0.8rem;
  }
}

@media screen and (min-width: 1008px) {
  @-webkit-keyframes appear {
    from { tranfsorm: translateX(100rem) }
    to { -webkit-transform: translateY(0); transform: translateY(0)}
  }
  @keyframes appear {
    from { tranfsorm: translateX(100rem) }
    to { -webkit-transform: translateY(0); transform: translateY(0)}
  }
}

.footer {
    width: 100%;
    padding: 1rem;
    background-color: rgb(52, 48, 48);
    justify-content: center;
    display: flex;
    bottom: 0;
    position: relative;
}

.footer-contain {
    margin: auto;
}

.center {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    padding: 1rem;
}

.icon {
    display: flex;
    flex-direction: row;
}
.hero {
    /* height: 100vh; */
    min-height: 80vh;
    width: 100%;
    background-size: cover;
}




.about-info {
    width: 100%;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

p {
    font-size: 20px;
}

.about-info .right {
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 80%;
}

.image-container {
    width: auto;
    padding-right: 15px;
    padding-left: 15px;
    /* padding-bottom: 15px; */
}

.left {
    display: flex;
    justify-content: center;
    margin: 2.5%;
    width: auto;
    padding-right: 15px;
    padding-left: 15px;
    /* padding-bottom: 15px; */
}

.imageFish {
    object-fit:contain;
    height: 50%;
    width: 50%;
    padding: 0, 0, 5% 0;
}

@media screen and (min-width: 1400px) {
    .about-info {
        margin: 2rem --8rem;
    }
}
form {
    width: 100%;
    height: 100%;
    margin-bottom: 40%;
    padding-top: 10%;
}

.contact {
    align-items: center;
    display:flex;
    flex-direction: column;
}

.email {
    padding-right: 40%;
    margin-bottom: .5%;
    width: 80%;
    display: flex;
    flex-direction: column;
}

.message {
    width: 80%;
    display: flex;
    flex-direction: column;
}

input {
    border-color: black;
    background-color: rgb(207, 204, 204);
    padding: .25rem;
}

textarea {
    background-color: rgb(207, 204, 204);
    border-color: black;
}

button {
    max-width: -webkit-min-content;
    max-width: min-content;
    margin-top: 15px;
    background-color: rgb(207, 204, 204);
    border-color: black;
}

/* project cards */

.project-container {
    max-width: 50%;
    margin: auto;
    display: flex;
    grid-template-columns: (3,1fr);
    grid-gap: 40px;
    padding-bottom: 2rem;
    z-index: 99999;
}

.project-card {
    margin: 1rem;
    background: rgb(205, 201, 201);
    padding: 1rem 1rem;
    border-radius: .75rem;
    z-index: 9;
    height: auto;
}

.project-text {
    color: black;
}

.project-card img {
    width: 100%;
    border-radius: .75rem;
}

.project-title {
    color: black;
    padding: 1rem;
    display: flex;
    justify-content: center;
}

.project-btns {
    display: flex;
    justify-content: space-around;
    padding: .5rem;
}

